*{
  padding: 0;
  margin: 0;
  box-sizing: border-box;
}
a{
  text-decoration: none;
  color: inherit;
}
li{
  list-style: none;
}
header{
  width: 100%;
  height: 10vh;
  background-color: #2d2d2d;
  display: flex;
  align-items: center;
  justify-content: space-between;
  color: #fff;
}
header .logo{
  margin-left: 3vw;
}
header nav{
  margin-right: 3vw;
}
header nav ul{
  display: flex;
}
header nav ul li{
  margin: 0 2vw;
}
